.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background-color: white;
  height: 90%;
  width: 700px;
  border-radius: 20px;
  padding: 30px;
  overflow-y: auto;
}

.card::-webkit-scrollbar {
  width: 8px;
  
}

.card::-webkit-scrollbar-thumb {
  background-color: hsl(219, 12%, 42%);
  border-radius: 10px;
}

.card::-webkit-scrollbar-thumb:hover {
  background-color: hsl(219, 85%, 26%);
}

.card::-webkit-scrollbar-track {
  background-color: hsl(219, 14%, 63%);
  width: 10px;
  border-radius: 10px;
}

.header {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

.logo {
  display: flex;
  align-items: center;
  gap: 20px;
}

.logo h3 {
  font-size: 1.5em;
  color: hsl(219, 85%, 26%);
}

.logo p {
  color: white;
  background-color: hsl(219, 85%, 26%);
  text-align: center;
  width: 30px;
  height: 22px;
  border-radius: 5px;
}

.read {
  all: unset;
  color: hsl(219, 12%, 42%);
  cursor: pointer;
}

.read:hover {
  color: hsl(219, 85%, 26%);
}

.notification {
  display: flex;
  align-items: center;
  gap: 20px;
  background-color: hsl(210, 60%, 98%);
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.notificationRead {
  display: flex;
  align-items: start;
  gap: 20px;
  /* background-color: hsl(0, 0%, 79%); */
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.notification img {
  width: 40px;
}

.notificationRead img{
  width: 40px;
}

.notificationRead .content p:nth-child(1)::after {
  width: 0;
  height: 0;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.name {
  color: hsl(224, 21%, 14%);
  font-weight: 800;
  margin-right: 5px;
  cursor: pointer;
}

.important {
  margin-left: 5px;
  font-weight: 800;
  cursor: pointer;
}

 p {
  color: hsl(219, 12%, 42%);
  position: relative;
  font-size: 15px;
}

.content p:nth-child(1)::after {
  content: "";
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: hsl(1, 90%, 64%);
  position: absolute;
  top: 6px;
  right: -20px;
}

.time {
  font-size: 14px !important;
}

.name:hover, .important:hover {
  color: hsl(219, 85%, 26%);
  
}

.read {
  background-color: transparent;
}

.read p:nth-child(1)::after {
  content: "";
  width: 0;
  height: 0;
  border-radius: 0;
  background-color: transparent;
  position: absolute;
  top: 0;
  right: 0;
}


.message {
  border: 1px solid hsl(205, 33%, 90%);
  padding: 10px;
  border-radius: 3px;
  cursor: pointer;
}

.message:hover{
  background-color: hsl(211, 68%, 94%);
}

.container{
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.chess{
  cursor: pointer;
  transition: .2s;
}

.chess:hover{
  scale: 1.1;
  background-color: hsl(219, 12%, 42%);
  padding: 2px;
  border-radius: 5px;
}